.dropdown-container {
    position: absolute;
    display: inline-block;
    right: 0;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    color: black;
}

.dropdown-content > div {
    margin: 0.5em;
}

.dropdown-container:hover .dropdown-content {display: block;}
